// https://observablehq.com/@robinl/gatsby-test@21
import define1 from "../@jashkenas/inputs.js";

export default function define(runtime, observer) {
  const main = runtime.module();
  main.variable(observer("header__o")).define("header__o", ["md"], function(md){return(
md`# Gatsby test`
)});
  main.variable(observer("text__o")).define("text__o", ["md"], function(md){return(
md`Some text goes here`
)});
  const child1 = runtime.module(define1);
  main.import("slider", child1);
  main.variable(observer("viewof a2")).define("viewof a2", ["slider"], function(slider){return(
slider({
  min: 0,
  max: 1e9,
  step: 1000,
  value: 3250000,
  format: ",",
  description:
    "Zero to one billion, in steps of one thousand, formatted as a (US) number"
})
)});
  main.variable(observer("a2")).define("a2", ["Generators", "viewof a2"], (G, _) => G.input(_));
  main.variable(observer()).define(["md","a2"], function(md,a2){return(
md`The value of the slider is ${a2}`
)});
  return main;
}
